// ImageChange001Store.js

import { makeAutoObservable, runInAction } from 'mobx';
import imgPaths from '../img/img';

class ImageChange001Store {
  backgroundArr = [
    require("../img/background1.jpg"),
    require("../img/background2.jpg"),
    require("../img/background3.jpg"),
    require("../img/background4.jpg"),
  ];

  backgroundImg = "";
  savedImg = [];
  ArrayLength = 3;

  constructor() {
    makeAutoObservable(this);
    this.setRandomBackground();
  }

  setRandomBackground() {
    const randomIndex = Math.floor(Math.random() * this.backgroundArr.length);
    this.backgroundImg = this.backgroundArr[randomIndex];
  }

  changeImage = () => {
    let randomImgUrl;
    do {
      const randomIndex = Math.floor(Math.random() * imgPaths.length);
      randomImgUrl = imgPaths[randomIndex].imageUrl;
    } while (this.savedImg.includes(randomImgUrl));

    console.log("Selected image:", imgPaths.find(img => img.imageUrl === randomImgUrl));
    console.log("Saved images:", this.savedImg); // 배열 값들 출력
    runInAction(() => {
      const updatedSavedImg = [...this.savedImg];
      if (updatedSavedImg.length >= this.ArrayLength) {
        updatedSavedImg.shift();
      }
      updatedSavedImg.push(randomImgUrl);
      this.savedImg = updatedSavedImg;
    });
  };
  
}

export default ImageChange001Store;
