import React, { useRef, useState } from 'react';
import { observer, Observer } from 'mobx-react';
import { runInAction } from 'mobx';
import ImageChange001Store from '../stores/ImageChangeStore';
import who from '../img/who.jpg';
import imgPaths from '../img/img'; // 이미지 파일의 경로

import Button from '@mui/material/Button';
import Snackbar from '@mui/material/Snackbar';
import { Alert } from '@mui/material';

const ImageChange001 = observer(() => {
  const store = new ImageChange001Store();
  const [randomPic, setRandomPic] = useState(who); // 기본 이미지로 'who'를 설정
  const [backgroundImage] = useState(store.backgroundImg);
  const [savedImg, setSavedImg] = useState([]); // 이미지 URL을 저장할 배열
  const ArrayLength = 3; // 이미지 저장 배열의 최대 길이
  const [입력값, 입력값변경] = useState('');
  const inputRef = useRef();
  const [openSuccessAlert, setOpenSuccessAlert] = useState(false); // 성공 알림창 상태
  const [openErrorAlert, setOpenErrorAlert] = useState(false); // 에러 알림창 상태



  const changeImage = () => {
    let randomImgUrl;
    do {
      const randomIndex = Math.floor(Math.random() * imgPaths.length);
      randomImgUrl = imgPaths[randomIndex].imageUrl;//벨류값 가져올거라고 지정해줌 
    } while (savedImg.includes(randomImgUrl)); // 겹치는 이미지가 있는지 확인 업데이트가 되는건 배열에 저 값이 있다면 다시 do실행 


    console.log("Selected image:", imgPaths.find(img => img.imageUrl === randomImgUrl));
    runInAction(() => {
      // const updatedSavedImg = [...savedImg]; // 이전에 savedimg에 있는 값을 가져옴 
      // if (updatedSavedImg.length >= ArrayLength) { // 만약 가져오는데 배열 길이가 3보다 더 크면 
      //   updatedSavedImg.shift(); // 가장 오래된 이미지 삭제 shift() == 첫번째 배열 삭제
      // }
      // updatedSavedImg.push(randomImgUrl); // 새로운 이미지 추가
      // setSavedImg(updatedSavedImg); // 상태 업데이트 현재 담긴 배열들로 
      setRandomPic(randomImgUrl);//이미지 랜덤으로 나와라 뿅
    });
  };

  const handleKeyPress = (event) => { // Enter키 누르면  
    if (event.key === 'Enter') {
      runInAction(() => {//액션줄거야\
        const inputValue = event.target.value;//입력된 값가져와
        console.log("정답입력:", inputValue);
        입력값변경(inputValue);// 입력한 값으로 입력값변경내용 변경
        const currentImgName = imgPaths.find(img => img.imageUrl === randomPic).name;//지금randomPic에 나와있는 이미지랑 imageUrl동일한거 찾고 그거의 네임 가져옴
        if (currentImgName === inputValue) { //똑같은지 확인하기 
          setOpenSuccessAlert(true); // 성공 알림창 열기
          inputRef.current.value = "";
          const updatedSavedImg = [...savedImg];
          if (updatedSavedImg.length >= ArrayLength) {
            updatedSavedImg.shift();
          }
          updatedSavedImg.push(randomPic);
          setSavedImg(updatedSavedImg);
        }
        else {
          setOpenErrorAlert(true); // 에러 알림창 열기
          inputRef.current.value = "";
        }
        // 이미지 변경
        changeImage();
      });
    }
  };




  return (
    <>
       <section style={{ backgroundImage: `url(${backgroundImage})`, width: '100%', height: '100vh', }}>
        <div style={{ height: '100%', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
          <div style={{
            marginBottom: '10px',
            fontSize: '40px',
            color: 'white',
            border: '2px solid white',
            padding: '10px',
            fontWeight: 'bold',
            textShadow: '2px 2px 4px rgba(0,0,0,1)',
            boxShadow: "10px 10px 10px 8px rgba(255,255,255,0.5)"            
          }}>
            아이돌의 이름은?
          </div>
          <div style={{ display: 'flex', justifyContent: 'center' }}>
            <Back />
            {/* 첫 번째 이미지 옵저버 */}
            <div>
              <Observer>
                {() => (
                  <img
                    src={randomPic}
                    alt="랜덤이미지"
                    width="350px"
                    height="350px"
                    style={{
                      position: 'absolute',
                      top: '50%',
                      left: '50%',
                      transform: 'translate(-50%, -70%)',
                      zIndex: "2",
                      
                    }}
                  />
                )}
              </Observer>

            </div>
            {/* 저장된 이미지 */}
            <div>
              <div style={{
                marginLeft: '190px',
                fontSize: '20px',
                color: 'white',
                border: '2px solid white',
                padding: '5px',
                fontWeight: 'bold',
                textShadow: '2px 2px 4px rgba(0,0,0,1)',
                boxShadow: "10px 10px 10px 8px rgba(255,255,255,0.5)"     
              
              }}>
                맞힌 아이돌
              </div>
              <div style={{ display: 'flex', flexDirection: 'column-reverse' }}>
                {savedImg.map((imgUrl, index) => (
                  <img
                    key={index}
                    alt="맞힌이미지"
                    src={imgUrl}
                    width="100"
                    height="100"
                    style={{ margin: '10px', marginLeft: '200px', boxShadow: "8px 10px 10px 8px rgba(0,0,0,0.5)", }}
                  />
                ))}
              </div>
            </div>
          </div>
          <Observer>
            {() => (
              <input
                ref={inputRef}
                style={{
                  width: "200px",
                  height: "35px",
                  fontSize: "30px",
                  position: "absolute",
                  top: '50%',
                  left: '50%',
                  transform: 'translate(-50%, 400%)',
                  zIndex: "2",
                }}
                onKeyPress={handleKeyPress}
                
              />
            )}
          </Observer>
          <Button variant="contained" onClick={changeImage} style={{ backgroundColor: "#faa693", marginTop: "80px", width: "200px", height: "50px", fontSize: "25px", fontWeight: "bold" }}>다음사진</Button>
          {/* 알림창 */}
          <Snackbar open={openSuccessAlert} autoHideDuration={2000} onClose={() => setOpenSuccessAlert(false)} anchorOrigin={{ vertical: 'top', horizontal: 'center' }}>
            <Alert severity="success" sx={{ width: '100%' }}>
              정답입니다!
            </Alert>
          </Snackbar>
          <Snackbar open={openErrorAlert} autoHideDuration={2000} onClose={() => setOpenErrorAlert(false)} anchorOrigin={{ vertical: 'top', horizontal: 'center' }}>
            <Alert severity="error" sx={{ width: '100%' }}>
              틀렸습니다. 다음 사진을 맞춰보세요!
            </Alert>
          </Snackbar>

        </div>
      </section>
    </>
  );
});

function Back() {
  const styles = {
    backgroundColor: "#ffffff",
    color: "white",
    padding: "10px",
    borderRadius: "2px",
    width: "400px",
    height: "500px",
    justifyContent: "center",
    boxShadow: "10px 10px 10px 10px rgba(0,0,0,0.5)",
    position: "relative",
    zIndex: "1",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)"
  };

  return (
    <div style={styles}></div>
  );
}

export default ImageChange001;